<style scoped>
.login-con {
  background: #fff;
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
}
.logo-img {
  padding: 40px 24px 14px;
}
.logo-img img {
  width: 50px;
  height: 50px;
}
</style>
</style>
<template>
  <div>
    <div class="content login-con">
      <!-- <div style="display:flex;">
      <img src="../assets/images/Logo.png" style="width:80px;height:80px;margin:auto;" />
      </div>-->
      <div class="logo-img">
        <img src="../assets/images/logo-img.svg" />
      </div>
      <div class="dlbtwk">
        <h2></h2>
      </div>
      <ul class="dlnrwk">
        <li>
          <input type="text" class="dlnrwk-li-input" placeholder="" v-model="uphone" />
        </li>
        <li>
          <span v-show="cansentcode" @click="btnSend"></span>
          <span v-show="!cansentcode" v-text="sentcodeTime+' s'"></span>
          <input type="text" class="dlnrwk-li-input1" placeholder="" v-model="ucode" />
        </li>
        <input
          type="button"
          class="dlnrwk-an"
          value=""
          style="margin-top: 32px;"
          @click="btnLogin"
        />
      </ul>
      <!-- <div class="zcwjmmwk">
       <span @click="btnForget"></span> 
        <i @click="btnReg"></i>
      </div>-->
    </div>
  </div>
</template>
<script>
import {
  userLogin,
  sendPhone,
  getIsFinishUserInfo,
  WxLoginUserInfo
} from "../api/api";
import { wxappid } from "../common/js/config";
import { MessageBox } from "mint-ui";
export default {
  data() {
    return {
      redirect: "",
      uphone: "",
      ucode: "",
      cansentcode: true,
      sentcodeTime: 60,
      code: "",
      openId: ""
    };
  },
  mounted() {
    this.redirect = this.$route.query.redirect;
    if (!this.redirect) this.redirect = "/jkd";
    if (this.isWechat()) {
      this.code = this.$route.query.code;
      this.wxlogin();
    }
  },
  methods: {
    isWechat() {
      var ua = navigator.userAgent.toLowerCase();
      if (ua.match(/MicroMessenger/i) == "micromessenger") {
        return true;
      } else {
        return false;
      }
    },
    wxlogin() {
      var validCode = localStorage.getItem("validCode");//
      if (validCode != 1 || !this.$util.isNotEmpty(this.code)) {
        //
        localStorage.setItem(
          "LoginRedirect",
          this.$route.query.redirect ? this.$route.query.redirect : "/jkd"
        );
        localStorage.setItem("validCode", 1);
        window.location.href =
          "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" +
          wxappid +
          "&redirect_uri=" +
          encodeURIComponent("http://" + window.location.host + "/login") +
          "&response_type=code&scope=snsapi_base&state=1#wechat_redirect";
      } else {
        localStorage.removeItem("validCode");
        WxLoginUserInfo({ value: this.code }).then(data => {
          if (data.status == 1 && this.$util.isNotEmpty(data.token)) {
            this.$toast({ duration: 1000, message: "" });
            this.$store.state.token = data.token;
            getIsFinishUserInfo({ token: data.token }).then(IsFinish => {
              if (!IsFinish) {
                this.$router.replace("/peopleInfo");
              } else {
                this.redirect = localStorage.getItem("LoginRedirect");
                if (this.redirect) {
                  this.$router.replace(this.redirect);
                } else {
                  this.$router.replace("/jkd");
                }
              }
            });
          } else {
            this.openId = data.openId;
          }
        });
      }
    },
    btnForget() {
      location.href = "http://m.1718china.cn/Account/ForgetPass?source=ysd";
    },
    btnReg() {
      location.href =
        "http://m.1718china.cn/Account/Register?source=ysd&phone=" +
        this.uphone;
    },
    valid() {
      if (this.uphone == "" || this.uphone == null) {
        this.$toast({ duration: 1000, message: "" });
        return false;
      }
      if (!this.$util.isPhoneNumber(this.uphone)) {
        this.$toast({
          duration: 1000,
          message: ""
        });
        return false;
      }
      if (this.ucode == "" || this.ucode == null) {
        this.$toast({ duration: 1000, message: "" });
        return false;
      }
      return true;
    },
    btnLogin() {
      if (!this.valid()) return;
      let lgparams = {
        phone: this.uphone,
        code: this.ucode,
        openId: this.openId
      };
      userLogin(lgparams).then(data => {
        if (data === "") {
          // MessageBox.confirm("?").then(
          //   action => {
          //     location.href =
          //       "http://m.1718china.cn/Account/Register?source=ysd&phone=" +
          //       this.uphone;
          //   }
          // );
          return;
        } else {
          if (!data) return;
          try {
            this.$toast({ duration: 1000, message: "" });
            this.$store.state.token = data;
            getIsFinishUserInfo({ token: data }).then(IsFinish => {
              if (!IsFinish) {
                this.$router.replace("/peopleInfo");
              } else {
                if (this.$route.query.redirect) {
                  this.$router.replace(this.$route.query.redirect);
                } else {
                  this.$router.replace("/jkd");
                }
              }
            });
          } catch (e) {
            console.log(e);
          }
        }
      });
    },
    btnSend() {
      if (this.uphone == "" || this.uphone == null) {
        this.$toast({ duration: 1000, message: "" });
        return false;
      }
      if (!this.$util.isPhoneNumber(this.uphone)) {
        this.$toast({
          duration: 1000,
          message: ""
        });
        this.$refs.uphone.focus();
        return false;
      }
      let params = {
        phone: this.uphone
      };
      _this = this;
      sendPhone(params)
        .then(data => {
          if (data) {
            this.cansentcode = false;
            var getcodeinval = setInterval(() => {
              this.sentcodeTime--;
              if (this.sentcodeTime <= 0) {
                this.cansentcode = true;
                clearInterval(getcodeinval);
              }
            }, 1000);
            _this.$toast({ duration: 1000, message: "" });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>


