<template>
  <div style="width:100%;height:100%;text-align: center;display:flex;margin-top:20px;">
    <h6 style="margin:auto;font-size:16px"></h6>
  </div>
</template>
<script>
import { Refill } from "../../api/api";
export default {
  data() {
    return {
      money: "",
      redirect: "",
      code: ""
    };
  },
  mounted() {
    try {
      //debugger;
      var data = localStorage.getItem("wxpay");
      data = JSON.parse(data);
      this.money = data.money;
      this.redirect = data.redirect;
      this.code = this.$route.query.code;
      if (!this.code) {
      _this.$toast("");
      setTimeout(() => {
        this.$router.back(-2);
      }, 500);
      } else {
        this.payment();
      }
    } catch (err) {
      _this.$toast("");
      setTimeout(() => {
        this.$router.back(-2);
      }, 500);
    }
  },
  methods: {
    payment() {
      let _this = this;
       let params = { amount: this.money, code: this.code };
       Refill(params).then(data => {
        if (!data) {
          _this.$toast("");
          setTimeout(() => {
            this.$router.back(-2);
          }, 500);
          return;
        }
        //
        this.$util.chooseWXPay({
          timestamp: data.timestamp, // 
          nonceStr: data.nonceStr, // 32 
          package: "prepay_id=" + data.prepay_id, // prepay_idprepay_id=***
          signType: "MD5", // 'SHA1''MD5'
          paySign: data.paySign, // 
          success: function(res) {
            //
            _this.$router.replace(_this.redirect);
          },
          cancel: function(res) {
            _this.$router.replace(_this.redirect);
          }
        });
      });
    }
  }
};
</script>



