<template>
  <div>
    <header>
      <a class="iconfont icon-back" @click="$router.replace('/jlj')"></a>
      <h1 class="tab-title"></h1>
      <button class="menu" style="background: #fff;" @click="$router.replace('/jlj')"></button>
    </header>
    <div class="content">
      <div class="jlj-sqcg">
        <img src="../../assets/images/wcjb.png" alt="" />
        <p><br /><span></span></p>
      </div>
      <ul class="jlj-txxq">
        <h2></h2>
        <li><span>{{NiceName}}</span></li>
        <li><span>{{CashAmount.toFixed(2)}}</span></li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
    data(){
        return{
         NiceName:"",
         CashAmount:0.00
        }
    },
    mounted(){
        $("body").css("background","#fff");
        if(this.$util.isNotEmpty(this.$route.query.nickName)){
          this.NiceName=this.$route.query.nickName;
        }
        if(this.$util.isNotEmpty(this.$route.query.CashAmount)){
          this.CashAmount =this.$route.query.CashAmount;
        }
    }
}
</script>



