<style scoped>
.mint-popup-bottom {
  top: auto;
  right: auto;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 0, 0);
  width: 100%;
}
.fpxz {
  padding: 14px;
  border-top: 1px solid #f7f7f7;
}
.fpxz h2 {
  padding-bottom: 14px;
  font-size: 14px;
  font-weight: bold;
}
.fpxz p {
  font-size: 13px;
  color: #666;
  padding-bottom: 4px;
  text-align: justify;
}
.fqxqwk li {
  display: flex;
  align-items: center;
}
.fqxqwk-li-textfield {
  margin-top: 0;
}
.fqxqwk li .fqxqwk-li-textfield::-webkit-input-placeholder {
  color: #ccc;
  font-size: 13px;
}
input[type="checkbox"] {
  cursor: pointer;
  position: relative;
  width: 14px;
  height: 14px;
  font-size: 14px;
}

input[type="checkbox"]::after {
  position: absolute;
  top: 0;
  background-color: #efeff8;
  color: #666666;
  width: 14px;
  height: 14px;
  display: inline-block;
  visibility: visible;
  padding-left: 0px;
  text-align: center;
  content: " ";
  border-radius: 3px;
  border: 1px solid #ddd;
}

input[type="checkbox"]:checked::after {
  content: "";
  font-size: 12px;
  font-weight: bold;
}
.sure_xx {
  margin-left: 10px;
  font-size: 14px;
}
.first_kp {
  color: red;
  font-size: 14px;
}
</style>
<template>
  <div>
    <header>
      <a class="iconfont icon-back" @click="$router.replace(redirect)"></a>
      <h1 class="tab-title"></h1>
      <a class="menu" @click="isShowfp=true"></a>
    </header>
    <div class="content">
      <p class="zzfp">,,</p>
      <ul class="fqxqwk">
        <li>
          <span></span>
          <input
            type="radio"
            value="DInvoince"
            class="iconfont radio"
            name="rdoType"
            style="padding-right: 6px;"
            v-model="invoicetype"
          />
          <label></label>

          <input
            type="radio"
            value="ZInvoince"
            class="iconfont radio"
            name="rdoType"
            style="padding-right: 6px;"
            v-model="invoicetype"
          />
          <label></label>
        </li>
        <li>
          <span></span>
          <label></label>
          <input
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
            v-model="invoiceName"
          />
        </li>
        <li>
          <span></span>
          <label></label>
          <input
            v-model="invoiceNumber"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
          />
        </li>
        <li>
          <span></span>
          <label></label>
          <input
            v-model="bankName"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
          />
        </li>
        <li>
          <span></span>
          <label></label>
          <input
            v-model="bankCode"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
          />
        </li>
        <li>
          <span></span>
          <label></label>
          <input
            v-model="registAddress"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
          />
        </li>
        <li>
          <span></span>
          <label></label>
          <input
            v-model="mobile"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
          />
        </li>
        <li>
          <span></span>
          <label></label>
          <p>{{ billType }}</p>
        </li>
        <li>
          <span></span>
          <label></label>
          <p>{{ Amount }}</p>
        </li>
        <li>
          <textarea class="wengbqy" placeholder="" v-model="invoiceRemark"></textarea>
        </li>
      </ul>
      <ul class="fqxqwk">
        <li v-if="isShowPeople">
          <span></span>
          <label for="textfield"></label>
          <input
            name="textfield"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
            v-model="revicePhone"
          />
        </li>
        <li v-if="isShowPeople">
          <span></span>
          <label for="textfield"></label>
          <input
            name="textfield"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
            v-model="reviceName"
          />
        </li>
        <li>
          <span></span>
          <label for="textfield"></label>
          <input
            name="textfield"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
            v-model="reviceEmail"
          />
        </li>
        <li v-if="isShowPeople">
          <span></span>
          <label for="textfield"></label>
          <input
            name="textfield"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder=""
            readonly="readonly"
            onfocus="this.blur();"
            v-model="reviceCity"
            @click="btnCity"
          />
        </li>
        <li v-if="isShowPeople">
          <span></span>
          <label for="textfield"></label>
          <input
            name="textfield"
            type="text"
            class="fqxqwk-li-textfield"
            placeholder="1501"
            v-model="reviceAddress"
          />
        </li>
        <li v-if="isFirstInvoice">
          <input type="checkbox" v-model="dataCheck" id="checkbox1" class="checkbox_box" />
          <label class="sure_xx">
            
            <a class="first_kp"></a>
          </label>
        </li>
      </ul>
      <p
        style="padding-left: 14px; padding-right: 14px; padding-top: 12px; margin-bottom:90px; margin-top: 12px;"
      >
        <input type="button" class="dlnrwk-an" value="" @click="btnSubmit" />
      </p>
    </div>
    <mt-popup v-model="isOkInfo" class="mint-popup">
      <div class="tanchuwk">
        <h2></h2>
        <p>
          
          <i>
            {{
            invoicetype == "DInvoince" ? "" : invoicetype == "ZInvoince" ? "" : ""
            }}
          </i>
        </p>
        <p>
          
          <i>{{ invoiceName }}</i>
        </p>
        <p>
          
          <i>{{ invoiceNumber }}</i>
        </p>
        <p v-if="isShowPeople">
          
          <i>{{ revicePhone }}</i>
        </p>
        <h3>
          <em @click="btnCancel"></em>
          <s @click="btnOk"></s>
        </h3>
      </div>
    </mt-popup>
    <mt-popup v-model="isShowAddress" position="bottom" class="mint-popup">
      <mt-picker
        :slots="addressSlots"
        valueKey="title"
        @change="onAddressChange"
        :showToolbar="true"
      >
        <Slot>
          <div class="me-tel">
            <div @click="btnPickCancle"></div>
            <div style="color:green;" @click="btnPickOk"></div>
          </div>
        </Slot>
      </mt-picker>
    </mt-popup>
    <mt-popup
      v-model="isShowfp"
      class="mint-popup"
      position="right"
      style="height:calc(100vh - 0px)"
      popup-transition="popup-fade"
    >
      <div class="fpxz">
        <h2>
          <a class="iconfont icon-back" @click="isShowfp=false" style="margin-right:10px;"></a>
        </h2>
        <p>1</p>
        <p>22025</p>
        <p>3(13811418661)</p>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import {
  //getProvinceList,
  GetCompanyInvoiceInfo,
  CompanyInvoiceSubimt
} from "../../api/api";
import { MessageBox } from "mint-ui";
export default {
  data() {
    return {
      redirect: "",
      idList: "",
      invoicetype: "",
      invoiceName: "",
      invoiceNumber: "",
      invoiceRemark: "",
      bankName: "",
      bankCode: "",
      registAddress: "",
      mobile: "",
      billType: "",
      Amount: "",
      reviceName: "",
      revicePhone: "",
      reviceEmail: "",
      reviceCity: "",
      reviceAddress: "",
      isShowPeople: true,
      isShowAddress: false,
      IsFirst: false,
      addressSlots: [],
      addressData: [],
      isOkInfo: false,
      isShowfp: false,
      isFirstInvoice: false,
      dataCheck: false
    };
  },
  watch: {
    invoicetype(val) {
      if (val == "DInvoince") {
        this.isShowPeople = false;
      } else {
        this.isShowPeople = true;
      }
    }
  },
  mounted() {
    this.redirect = this.$route.query.redirect;
    this.idList = this.$route.query.idList;
    this.addressData = addressList;
    this.addressSlots = [
      {
        flex: 1,
        values: this.addressData,
        className: "slot1",
        textAlign: "center"
      },
      {
        flex: 1,
        values: this.addressData[0].list,
        className: "slot2",
        textAlign: "center"
      },
      {
        flex: 1,
        values: this.addressData[0].list[0].list,
        className: "slot3",
        textAlign: "center"
      }
    ];
    GetCompanyInvoiceInfo(this.idList).then(data => {
      if (!data) return;
      this.invoiceNumber = data.invoiceNumber;
      this.registAddress = data.cmpyAddress;
      this.mobile = data.cmpyTel;
      this.bankName = data.bank;
      this.bankCode = data.bankAccount;
      this.Amount = data.invoiceMoney.toFixed(2);
      this.billType = data.invoiceProject;
      this.invoiceName = data.invoiceName;
      this.reviceName = data.name;
      this.revicePhone = data.invoicePhone;
      this.isFirstInvoice = data.isFirstInvoice;
    });
  },
  methods: {
    btnCancel() {
      this.isOkInfo = false;
    },
    btnOk() {
      let params = {
        rechargeCodeList: this.idList,
        billType: this.invoicetype,
        invoiceName: this.invoiceName,
        invoiceNumber: this.invoiceNumber,
        invoiceRemark: this.invoiceRemark,
        bank: this.bankName,
        bankAccount: this.bankCode,
        cmpyAddress: this.registAddress,
        cmpyTel: this.mobile,
        name: this.reviceName,
        phone: this.revicePhone,
        email: this.reviceEmail,
        address: this.reviceCity + this.reviceAddress
      };
      CompanyInvoiceSubimt(params).then(data => {
        if (!data) {
          MessageBox.alert(this.$store.state.lastError.message).then();
          return;
        }

        MessageBox.alert("").then(action => {
          this.$router.push({
            path: "/InvoiceHistory",
            redirect: "mycompany"
          });
        });
      });
    },
    Valid() {
      if (this.invoicetype == 0) {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.invoiceName == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.invoiceNumber == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.bankName == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.bankCode == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.registAddress == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.mobile == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.invoicetype == "ZInvoince" && this.reviceName == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.invoicetype == "ZInvoince" && this.revicePhone == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.reviceEmail == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      } else if (!this.$util.isEmail(this.reviceEmail)) {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.invoicetype == "ZInvoince" && this.reviceCity == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.invoicetype == "ZInvoince" && this.reviceAddress == "") {
        this.$toast({ duration: 1200, message: "" });
        return false;
      }
      if (this.isFirstInvoice && !this.dataCheck) {
        this.$toast({
          duration: 1200,
          message: ""
        });
        return false;
      }
      return true;
    },
    btnSubmit() {
      if (this.Valid() == true) {
        this.isOkInfo = true;
      }
    },
    btnCity() {
      this.isShowAddress = true;
    },
    onAddressChange(picker, values) {
      if (values.length < 1) return;
      picker.setSlotValues(1, values[0].list);
      picker.setSlotValues(2, values[1].list);
      let arr = picker.getValues();
      this.reviceCity = `${arr[0].title}-${arr[1].title}-${arr[2].title}`;
    },
    btnPickCancle() {
      this.isShowAddress = false;
      this.reviceCity = "";
    },
    btnPickOk() {
      if (this.reviceCity === "" && this.addressData.length > 0) {
        this.reviceCity = `${this.addressData[0].title}-${this.addressData[0].list[0].title}-${this.addressData[0].list[0].list[0].title}`;
      }
      this.isShowAddress = false;
    }
  }
};
</script>



