const wx = require('weixin-js-sdk')
import { getwechatInfo } from '../../api/api'
import { Toast } from 'mint-ui'
import { MessageBox } from "mint-ui";

export default {
    data(){
        return{
            isReady:false
        }
    },
    config() {
        //debugger
        let u = navigator.userAgent;
        let url=window.location.href;
        let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
			if (isiOS) {
			 url=window.location.href.split('#')[0] || window.location.href;
			} 
        let params = { url: url }
       
        getwechatInfo(params).then(data => {
            wx.config({
                debug: false, // ,apialertpclogpc
                appId: data.appId, // 
                timestamp: data.timestamp, // 
                nonceStr: data.nonceStr, // 
                signature: data.signature,// 1
                jsApiList: ["previewImage", "chooseImage", "uploadImage", "downloadImage", "onMenuShareAppMessage", "onMenuShareTimeline", "chooseWXPay"] // JSJS2
            });
        })
    },
    chooseImage(info) {
        let _info = info;
        wx.error(function (res) {
            // configerrorconfigdebugresSPA
            Toast(" ");
        });
        wx.ready(() => {
            wx.chooseImage({
                count: _info.count,
                sizeType: ['original', 'compressed'],
                sourceType: ['album', 'camera'],
                success: _info.success
            });
        });
        this.config();
    },
    uploadImage(info) {
        let _info = info;
        wx.error(function (res) {
            // configerrorconfigdebugresSPA
            Toast("  ");
        });
        wx.ready(() => {
            wx.uploadImage({
                localId: _info.chooseImageId,
                isShowProgressTips: 1,
                success: _info.success
            });
        });
        this.config();
    },
    chooseWXPay(info) {
        let _info = info;
        this.config();
        wx.error(function (res) {
            //debugger
            // configerrorconfigdebugresSPA

        });
        wx.ready(() => {
            //alert("" + JSON.stringify(_info))
            wx.chooseWXPay({
                timestamp: _info.timestamp, // 
                nonceStr: _info.nonceStr, // 32 
                package: _info.package, // prepay_idprepay_id=***
                signType: _info.signType, // 'SHA1''MD5'
                paySign: _info.paySign, // 
                success: function (res) {
                    //
                    Toast("");
                    if (_info.success) _info.success();
                },
                cancel: function (res) {
                    Toast("");
                    if (_info.cancel) _info.cancel();
                }
            });
        });
    },
    wxshare(info) {
        let _info = info;
        //debugger
        this.config();
        wx.ready(() => {
            const share_title = _info.title
            const share_desc = _info.desc
            const share_link = _info.link
            const share_img = _info.imgUrl

            wx.onMenuShareAppMessage({
                title: share_title, // 
                desc: share_desc, // 
                link: share_link, // 
                imgUrl: share_img, // 
                success: function () {
                    Toast('')
                    if (_info.success) _info.success();
                },
                cancel: function () {
                    Toast('')
                    if (_info.cancel) _info.cancel();
                }
            });

            wx.onMenuShareTimeline({
                title: share_title, // 
                link: share_link, // 
                imgUrl: share_img, // 
                success: function () {
                    Toast('')
                    if (_info.success) _info.success();
                },
                cancel: function () {
                    Toast('')
                    if (_info.cancel) _info.cancel();
                }
            })
            // wx.onMenuShareTimeline({
            //     title: share_title, // 
            //     link: share_link, // 
            //     imgUrl: share_img, // 
            //     success: function () {
            //         Toast('')
            //         if (info.success) info.success();
            //     },
            //     cancel: function () {
            //         Toast('')
            //         if (info.cancel) info.cancel();
            //     }
            // })
            // wx.onMenuShareAppMessage({
            //     title: share_title, // 
            //     desc: share_desc, // 
            //     link: share_link, // 
            //     imgUrl: share_img, // 
            //     success: function () {
            //         Toast('')
            //         if (info.success) info.success();
            //     },
            //     cancel: function () {
            //         Toast('')
            //         if (info.cancel) info.cancel();
            //     }
            // })
        })
    }
}




